import React from "react";
import PropTypes from "prop-types";
// import { Link } from "react-router-dom";

export default function Navbar(props) {
  return (
    <nav
      className={`navbar navbar-expand-lg navbar-${props.setmodeColor} bg-${props.setmodeColor}`}
    >
      <div className="container d-flex justify-content-between align-items-center">
        <a className="navbar-brand" href="/">
          {props.title}{" "}
        </a>
        {/* <Link className="navbar-brand" to="/">
          {props.title}{" "}
        </Link> */}
        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span className="navbar-toggler-icon"></span>
        </button>
        <div
          className="collapse navbar-collapse mx-3"
          id="navbarSupportedContent"
        >
          <ul className="navbar-nav me-auto mb-2 mb-lg-0">
            <li className="nav-item">
              <a className="nav-link active" aria-current="page" href="/">
                Home
              </a>
              {/* <Link className="nav-link active" aria-current="page" to="/">
                Home
              </Link> */}
            </li>
            {/* <li className="nav-item">
              <Link className="nav-link active" to = "/Kaunt">
                {props.aboutTitle}
              </Link>
            </li> */}
          </ul>
        </div>
        <div className={`form-check form-switch text-${props.setmodeColor ==='light'?'black':'light'} `}>
          <input className="form-check-input" onClick={props.toggleMode} type="checkbox"  role="switch" id="flexSwitchCheckDefault"/>
          <label className="form-check-label" htmlFor="flexSwitchCheckDefault">
           {props.btnText}
          </label>
        </div>
      </div>
    </nav>
  );
}
Navbar.propTypes = {
  title: PropTypes.string.isRequired,
  aboutTitle: PropTypes.string,
};
