import "./App.css";
import Navbar from "./Component/Navbar";
import Kaunt from "./Component/Kaunt";
import Textform from "./Component/Textform";
import Alert from "./Component/Alerts";
import React, { useState } from "react";
// import { BrowserRouter as Router, Routes, Route } from "react-router-dom";

function App() {
  const [modeColor, setColor] = useState("light");
  const [btnText, setBtntext] = useState("Enable Dark Mode");
  const [alert, setAlert] = useState(null);

  const toggleColor = (col, msg) => {
    setColor(col);
    setBtntext(msg);
  };
  const toggleAlert = (message, type) => {
    //when you need to update multiple state variables at once, using an object can be advantageous.
    setAlert({
      message: message,
      type: type,
    });
  };

  const toggleMode = () => {
    // (modeColor === 'dark')?setColor('light',setBtntext("Enable Dark Mode")) :setColor('dark',setBtntext("Enable Light Mode"));
    modeColor === "dark"
      ? toggleColor("light", "Enable Dark Mode")
      : toggleColor("dark", "Enable Light Mode");
    document.body.style.backgroundColor =
      modeColor === "light" ? "#212529" : "white";
    modeColor === "dark"
      ? toggleAlert("Light Mode has been activated", "sucess")
      : toggleAlert(" Dark Mode has been activated", "primary");
    modeColor === "light"
      ? (document.title = "TextUtils-DarkMode")
      : (document.title = "TextUtils-LightMode");
  };

  return (
    <>
      {/* <Router> */}
    
        <Navbar title="TextUtensils2" aboutTitle="Click Counter" setmodeColor={modeColor} toggleMode={toggleMode} btnText={btnText} />
       
        <Alert alert={alert} />
        <div className="container my-3">
          {/* <Routes>
            <Route exact path="/kaunt" element = {  <Kaunt setmodeColor={modeColor} />}>
             
            </Route>

            <Route exact path="/"
            element =  { <Textform heading="Enter the text" setmodeColor={modeColor} />} >
            </Route>
          </Routes> */}
          {/* <Kaunt setmodeColor={modeColor} /> */}
          <Textform heading="Enter the text" setmodeColor={modeColor} />
        </div>
      {/* </Router> */}

    </>
  );
}

export default App;
