import React, { useState } from "react";
import Button from '@mui/material/Button';
import Tooltip from '@mui/material/Tooltip';
import Fade from '@mui/material/Fade';
import { styled } from '@mui/material/styles';

// Custom styled Tooltip
const CustomTooltip = styled(Tooltip)({
  "& .MuiTooltip-tooltip": {
    backgroundColor: "#007bff", // Primary button color
  },
});

export default function Textform(props) {
  const [text, setText] = useState("");
  const [tooltipOpen, setTooltipOpen] = useState(false);
  const wordCount = text.split(" ").filter(word => word.match(/[a-zA-Z0-9]+/)).length;

  const convertoUp = () => {
    let UpText = text.toUpperCase();
    setText(UpText);
  };
  
  const convertoLow = () => {
    let UpText = text.toLowerCase();
    setText(UpText);
  };
  const removeExtraSpace = () => {
    let UpText = text.split(/[ ]+/);      //for one or more space
    setText(UpText.join(" "));        //for combining substrings with only one space
    // let UpText = text.replace(/\s/g, '') for all removal of space
    // setText(UpText);
    //* REGEX USED */}
  };
  
  const titleText = () => {
    const UpText = text.split(' ');
    const titleCaseText = UpText.map(word => {
      return word.charAt(0).toUpperCase() + word.slice(1).toLowerCase();
    });
    setText(titleCaseText.join(' '));
  };
  
  const clearText = () => {
    setText("");
  };

  const copyText = () => {
    navigator.clipboard.writeText(text)
      .then(() => {
        setTooltipOpen(true);
        setTimeout(() => {
          setTooltipOpen(false);
        }, 1000);
      })
      
  };

  const handletoUp = (e) => {
    setText(e.target.value);
  };

  
  return (
    <>
      <div className= "container p-2" style={{color: props.setmodeColor ==='light'?'black':'white'}}>
        <div className="col d-flex justify-content-between my-2">
        <h1>{props.heading}</h1>
        {/* <button className="btn btn-primary m-2" onClick={props.togleStyle}>{btnText}</button> */}
        </div>
        <div className="mb-3">
          <textarea style={{backgroundColor: props.setmodeColor ==='light'?'white':'grey'}}
            className="form-control"
          {...(props.setmodeColor==='light'? { placeholder:"Enter the text", style:{color:'black'}}:{placeholder:"Enter the text"})}
            value={text}
            onChange={handletoUp}
            id="exampleFormControlTextarea1"
            rows="3"
          ></textarea>
        </div>
        <div className="container">
            <div className="row g-2 ">
        <div className=" col-12 col-md-4 col-lg-3 converterBtn ">
        <button className="btn btn-primary btner" onClick={convertoUp}>Covert to Uppercase</button>
        </div>
        <div className=" col-12 col-md-4 col-lg-3 converterBtn ">
        <button className="btn btn-primary btner" onClick={convertoLow}>Covert to Lowercase</button>
        </div>
        <div className=" col-12 col-md-4 col-lg-3 converterBtn ">
        <button className="btn btn-primary btner" onClick={titleText}>Convert to Titlecase</button>
        </div>
        <div className=" col-12 col-md-4 col-lg-3 converterBtn ">
        <button className="btn btn-primary btner" onClick={removeExtraSpace}>Remove Extra Spaces</button> 
        </div>
          
       
        <div className=" col-12 col-md-4 col-lg-3 converterBtn ">
        <CustomTooltip
          TransitionComponent={Fade}
          TransitionProps={{ timeout: 600 }}
          title=  "Text copied!"
          open={tooltipOpen}
        >
          <Button  className="btn btn-primary btner" onClick={copyText} variant="contained" sx={{ bgcolor: '#007bff' }}>Copy to Clipboard</Button>
        </CustomTooltip>
        </div>
        <div className=" col-12 col-md-4 col-lg-3 converterBtn ">
        <button className="btn btn-primary btner " onClick={clearText}>Clear all Text</button>
        </div>
        </div>
        </div>
        
      <div className="container my-3">
        <h1>Your Words </h1>
        <p>{wordCount} words  {text.replace(/\s/g, '').length} characters</p>
        <p>{text === "" ? "0" : text.replace(/\s/g, '').length * 0.008} Read word per minute</p>
      </div>
      </div>
    </>
  );
}


